	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Sydney and Hannover","date":"06 Nov 2004","slug":"/2004-11-06-sydney-and-hannover/","tags":"Y2004"},"html":"<p>Mama left Thursday for her 10 day business trip in Sydney. So we men have to fend for ourselves for a few days. Our highlight today combines fun and technology. This morning we'll be taking the BUS from Buchholz to Harburg, then board the I.C.E. TRAIN to Hannover, where we will then hop on the STREETCAR to head for a day at the ZOO. If it rains, we'll just ride the streetcar all afternoon. Then we do the whole thing again in reverse.</p>\n<p>With a couple of Oma visits, a lantern march in kindergarten, an appointment at the ear-nose-throat specialist, and today's expedition, the week is going to go by very quickly. Then on Sunday we'll get up early and head to the airport to pick up Mama, whether she wants to be picked up or not.</p>"},"next":{"fields":{"title":"Party like it's 1989","date":"05 Nov 2004","slug":"/2004-11-05-party-like-its-1989/"}},"previous":{"fields":{"title":"The Values-Vote Myth","date":"06 Nov 2004","slug":"/2004-11-06-the-valuesvote-myth/"}}},{"node":{"fields":{"title":"Party like it's 1989","date":"05 Nov 2004","slug":"/2004-11-05-party-like-its-1989/","tags":"Y2004"},"html":"<p>In an inspired moment of creative fiscal policy, Finance Minister Eichel yesterday (with the approval of his boss the Chancellor) proposed moving the German Unity Day holiday to always fall on Sunday. This year a number of paid holidays fall on the weekend, thus adding a couple of tenths to national GDP, so why not have have a holiday fall always on the weekend? Alas, the idea fell like a lead balloon full of rocks. Maybe Eichel should hire some of those Greek statisiticians to rewrite the German calendar. My idea: move Unity Day to the day after Ascension, a nominal work day when noone works anyway. </p>\n<p><strong>Update:</strong> <a href=\"http://fistfulofeuros.net/archives/000913.php\" title=\"A Fistful of Euros: Another Brick In The Wall.\">Tobias</a> thinks it was all a ruse by Schr&ouml;der to trick the <em>L&auml;nder</em> into giving up a holiday in May. Maybe so, and the Union is all so easy to trick these days, but it still must be nice to have a chump like Eichel to always be the fall guy for you when things go wrong.</p>\n<p><a title=\"Plan to Move Unity Day Sets Off Political Firestorm | Current Affairs | Deutsche Welle |\" href=\"http://www.dw-world.de/dw/article/0,1564,1386127,00.html\">(DW-Online: Plan to Move Unity Day Sets Off Political Firestorm)</a></p>"},"next":{"fields":{"title":"Lower the Bar! Fire the Statisticians!","date":"05 Nov 2004","slug":"/2004-11-05-lower-the-bar-fire-the-statisticians/"}},"previous":{"fields":{"title":"Sydney and Hannover","date":"06 Nov 2004","slug":"/2004-11-06-sydney-and-hannover/"}}},{"node":{"fields":{"title":"Lower the Bar! Fire the Statisticians!","date":"05 Nov 2004","slug":"/2004-11-05-lower-the-bar-fire-the-statisticians/","tags":"Y2004"},"html":"<p>Remember the Lisbon Agenda, in which the EU pledged to become the world's most competitive economy by 2010? OK, stop laughing. Germany has come up with a plan on how the agenda can be met, namely by lowering the goal, a tactic which seems to work for domestic financial problems. So instead of competing right away with the United States, the EU can start by competing with, say, Khazakstan or Bangladesh. I personally think a better solution would be for the EU to hire <a href=\"http://www.eubusiness.com/afp/041021090354.2ted4mo5\" title=\"EUbusiness - EU attacks Greece over dodgy data\">Greek statisticians</a>. </p>\n<p>Maybe the tactic can be applied to other issues as well, for example, Turkey. \"Did you think we said talks would open in this century? No, we never meant that...\" (<a href=\"http://www.eubusiness.com/afp/041103200018.3clea50y\" title=\"EUbusiness - Germany wants to lower Lisbon Agenda targets: sources\">EUbusiness - Germany wants to lower Lisbon Agenda targets</a>)</p>"},"next":{"fields":{"title":"Is Italy a Red State?","date":"05 Nov 2004","slug":"/2004-11-05-is-italy-a-red-state/"}},"previous":{"fields":{"title":"Party like it's 1989","date":"05 Nov 2004","slug":"/2004-11-05-party-like-its-1989/"}}}],"pathPrefix":"","first":false,"last":false,"index":389,"pageCount":1101}}

